<template>
  <div class="MusicCD" @click="showMusicCd">
      <div class="music-cd">
        <div class="cds">
          <img 
            :src="currentPlayDetail.musicCoverImg" 
            crossorigin="anonymous" 
            ref='currentPlayImg' 
            @load="getImgColor"
            :class="{'img-rotate-stop':isMusicPaused}"
          >
        </div>
      </div>
      <div class="lrc-and-icons">
        <div class="lrc">
            <ul>
                <li class="active">Lorem ipsum dolor sit amet</li>
                <li>Lorem, ipsum dolor.</li>
            </ul>
        </div>
        <ul class="icons">
            <li ref="li" @click='icon'><i class="bi bi-download"></i></li>
            <li ref="li" @click='icon'><i class="bi bi-chat-dots"></i></li>
            <li ref="li" @click='icon'><i class="bi bi-heart"></i></li>
            <li ref="li" @click='icon'><i class="bi bi-three-dots"></i></li>
        </ul>
      </div>
  </div>
</template>

<script>
import { mapGetters, mapState, mapMutations, mapActions } from "vuex";
import colorthief from 'colorthief'
export default {
  data(){
    return{
      icons:'',
      color:'',
    }
  },
  props:['currentPlayDetail'],
  mounted(){
    this.icons = Array.from(this.$el.querySelectorAll('.icons .bi'))
    console.log(this.currentPlayDetail)
  },
  methods:{
    showMusicCd(e){
      // console.log(this.icons.includes(e.target))
      if(!this.icons.includes(e.target)){
          this.isLrcShow = !this.isLrcShow
      }
      
    },
    icon(){

    }, 
    async getImgColor(){
      const color = new colorthief()
      const currentPlayImg = this.$refs.currentPlayImg
      let res = await color.getPalette(currentPlayImg, 3)
      const [c1, c2, c3] = res.map((c)=>{
          return `rgba(${c[0]}, ${c[1]}, ${c[2]},0.7)`
      })
      this.color = c1
      console.log(c1,c2,c3)
      this.$bus.$emit('getMusicImgColor',this.color)
      // this.$refs.currentPlayImg.style.setProperty('--color',c2)
      // this.$refs.MusicListDetail.style.setProperty('--color2',c1)
    },
  },
  computed:{
    ...mapState(['isMusicPaused']),
    isLrcShow:{
      get(){
        return this.$store.state.isLrcShow
      },
      set(value){
        this.$store.state.isLrcShow = value
      }
    }
  }
}
</script>

<style lang='less' scoped>
    
    .MusicCD{
        width: 90vw;
        height:63vh;
        margin:0 auto;

      .music-cd{
        height: 32vh;
        aspect-ratio: 1;
        border-radius:50%;
        background-color:#eff2fe;
        box-shadow:19px 9px 20px #36363640, inset 1px 1px 0 #fff;
        padding:1vh;
        margin:0 auto;

        @keyframes imgRotate{
          from{
            transform:rotate(0)
          }
          to{
            transform:rotate(1turn)
          }
        }
        img{
          height: 32vh;
          aspect-ratio: 1;
          border-radius:50%;
          animation: 20s imgRotate infinite linear;
        }
        .img-rotate-stop{
          animation-play-state: paused;
        }
      }
      .lrc-and-icons{
        display:flex;
        justify-content: space-between;
        align-items: center;
        height:30vh;
        .lrc{
            li{
                min-height:6vh;
                transition:.3s;
                color:#0000005c;
                // white-space: nowrap;
            }
            .active{
                transform-origin: 0 0;
                transform:scale(1.2);
                color:black;
            }
        }

        .icons{
            text-align: center;
            height:28vh;
            display: flex;
            flex-direction: column;
            justify-content: space-between;
            li{
                height: 5vh;
                aspect-ratio: 1;
                border-radius:10px;
                background-color:#f6fafd;
                box-shadow:  5px 5px 12px #3636361d, inset 2px 2px 2px #fff, -5px -5px 20px #ffffff;
                line-height: 5vh;
                i{
                  width: 100%;
                  height: 100%;
                  display: block;
                }
            }
        }
      }
    }

</style>