<template>
    <div class="HomeContainer" id='APP'>
      <TopNav></TopNav>
      <Banner></Banner>
      <RecommendMusicList></RecommendMusicList>
      <NewMusicRecommend></NewMusicRecommend>
    <router-view></router-view>
      <a class="return-top" href="#APP">TOP</a>
      <ul>
        <li v-for="(single,index) in singleMusicRecList" :key='index' class="single-music-rec-comp">
          <SingleMusicRecommend :single='single'></SingleMusicRecommend>
        </li>
      </ul>
      <div class="loading">加载中...</div>
    </div>
</template>

<script>
  import axios from 'axios'
  import {mapGetters, mapState,mapMutations,mapActions} from 'vuex'

  import TopNav from './TopNav.vue'
  import Banner from './Banner.vue'
  import RecommendMusicList from './RecommendMusicList.vue'
  import NewMusicRecommend from './NewMusicRecommend.vue'
  import SingleMusicRecommend from './SingleMusicRecommend.vue'


  export default {
    name:'HomeContainer',
    components:{
      TopNav,Banner,RecommendMusicList,NewMusicRecommend,SingleMusicRecommend
    },
    data(){
      return {
      }
    },
    computed:{
      ...mapState(['singleMusicList','singleMusicRecList',]),
      offset:{
        get(){
          return this.$store.state.offset
        },
        set(value){
          console.log(value)
          this.$store.state.offset = value
        }
      }
    },
    methods:{
      ...mapActions(['getSingleMusicRecList','getSingleMusicList']),
      loading(){
        const load = this.$el.querySelector('.loading')
        const intersectionObserver = new IntersectionObserver((entries,observe) => {
            if (!entries[0].isIntersecting){
                return 
            }else{
              this.getSingleMusicRecList({musicList:this.singleMusicList, offset:this.offset})
            }

        });
        intersectionObserver.observe(load);
      }
    },
    mounted(){
      this.loading()
      this.getSingleMusicList()
      console.log('HomeContainer组件已挂载')
    },
    activated(){
      console.log('HomeContainer组件被激活')
      // this.getSingleMusicList()
    }

  };
</script>

<style scoped>

  .single-music-rec-comp:last-child{
    margin-bottom:10vw ;
  }
  .loading{
    text-align: center;
    font-size:4vw;
    height: 40vw;
    width:100vw;
  }
  .return-top{
    position:sticky;
    top:150vw;
    left: 87vw;
    display: block;
    height: 10vw;
    aspect-ratio: 1;
    background: #ddd;
    border-radius: 50%;
    text-align: center;
    line-height: 10vw;
    z-index: 10;
    float: left;
  }
</style>