<template>
  <div class="single-music-recommend" ref="singleMusic">
    <div class="singleMusicRecContainer">
        <div class="musicName-singerName">
            <i class="bi bi-play-fill"></i>
            <span>{{single.al.name}} - {{single.name}}</span>
        </div>
        <div class="mask-bar">
            <div class="author">
                <img src="http://p1.music.126.net/axewGX7u9P9Iuqjep-3mmQ==/109951165601796681.jpg" alt="">
                <span>{{single.al.name}}</span>
                <span>发布</span>
            </div>
            <div class="music-title">
                {{single.name}}
            </div>
            <div class="others-info">
                <div class="playList-name"><i class="bi bi-globe2"></i> 古典清香 I</div>
                <div class="comment-playeds">
                    <span><i class="bi bi-chat-left-dots"></i> {{musicComments}}</span>
                    <span><i class="bi bi-play-circle"></i> {{musicPlayeds}}</span>
                </div>
            </div>
        </div>
    </div>
  </div>
</template>

<script>
import axios from 'axios'
import service from '@/request'
import home from '@/request/api/home'
export default {
    data(){
        return {
            musicComments:0,
            musicPlayeds:0,
        }
    },
    props:['single'],
    mounted(){
        this.$nextTick(()=>{
            // axios.get(`http://192.168.0.103:3000/song/detail?ids=${this.single.id}`)
            home.getSingleMusicRecommend(this.single.id)
            .then(
                result=>{
                    const intersectionObserver = new IntersectionObserver((entries,observe) => {
                        if (!entries[0].isIntersecting){
                            return 
                        }else{
                            this.$refs.singleMusic.style.backgroundImage = `url(${result.data.songs[0].al.picUrl})`
                        }
                    }, {
                        rootMargin: '500px', // 指定视窗边界的偏移量
                    });
                    intersectionObserver.observe(this.$refs.singleMusic);
                },
                err=>{
                    this.recommendMusicList = '网络错误'
                    console.log(err.message)
                }
            ).then(
                response=>{
                    // axios.get(`http://192.168.0.103:3000/comment/music?id=${this.single.id}`)
                    home.getSingleMusicRecommendCommentTotal(this.single.id)
                    .then(
                        result=>{
                            this.musicComments = result.data.total
                        },
                        err=>{
                            console.log(err.message)
                        }
                    )
                },
                err=>{
                    console.log(err.message)
                }
            )
        })
        
    }
}
</script>

<style lang='less' scoped>
    .single-music-recommend{
        height:60vw;
        width:90vw;
        margin:0 auto 5vw;
        // background-image: url(http://p4.music.126.net/tnRgUYdnP7TdtXDwkDPevA==/109951168730396917.jpg);
        background-size:cover;
        border-radius: 20px;
        position: relative;

        .musicName-singerName{
            position: absolute;
            max-width: 80vw;
            overflow: hidden;
            text-overflow: ellipsis;
            white-space: nowrap;
            bottom: 30vw;
            font-size:2.5vw;
            padding:1.5vw 2vw;
            background-color:#d3d3d3aa;
            border-radius: 5vw;
            margin-left: 3vw;
            box-shadow: inset 2px 2px 3px #ffffff90;
        }
        .mask-bar{
            height: 27vw;
            width: 90vw;
            background: #ffffff80;
            backdrop-filter: blur(15px);
            position:absolute;
            bottom:0;
            border-radius: 15px;
            box-shadow: inset 0px 1px 3px #ffffffaa;
            border:1px solid #ffffffaa;

            .author,.music-title,.others-info{
                margin:1.5vw 3vw;
            }
            .author{
                // margin-bottom: 2vw;
                display: flex;
                align-items: center;
                color:#616767;
                font-size:3vw;
                >*{
                    margin:1vw 0.5vw;
                }
                img{
                    height: 5vw;
                    aspect-ratio: 1;
                    border-radius: 50%;
                }
            }
            .music-title{
                font-size:3.7vw;
            }
            .others-info{
                display:flex;
                justify-content: space-between;
                align-items: center;
                font-size:2.5vw;
                margin-top:3vw;
                
                .playList-name{
                    // height:5vw;
                    max-width: 50vw;
                    padding:0 2vw;
                    background-image: linear-gradient(to right,#b68380,#c49d96);
                    border-radius: 10vw;
                    color:#fbf8f2;
                    line-height: 6vw;
                    white-space: nowrap;
                    text-overflow: ellipsis;
                    overflow: hidden;
                    box-shadow: inset 1px 1px 2px #fbf8f2aa;
                }
                .comment-playeds{
                    min-width: 30vw;
                    font-size:3vw;
                    span{
                        margin:0 2vw;
                    }
                }
            }
        }
    }

</style>