<template>
  <div class="MusicListDetail clearfix" ref="MusicListDetail">
    <el-table
      v-loading="loading"
      v-show="loading"
      element-loading-text="拼命加载中"
      element-loading-spinner="el-icon-loading"
      element-loading-background="rgba(0, 0, 0, 0.8)"
      style="width: 100%;height:100vh">
    </el-table>

    <div class="musicListDetail-top">
      <div class="backword">
        <i class="bi bi-arrow-left" @click="backword"></i>
        <span>歌单</span>
      </div>
      <div class="searchListMusic">
        <i class="bi bi-search"></i>
        <i class="bi bi-three-dots-vertical"></i>
      </div>
    </div>

    <div class="musicList-info">
      <div class="author-title">
        <div class="musicListCover-img">
          <img :src="musicListCoverImg" crossorigin="anonymous" ref="musicListCoverImg" @load="getImgColor">
        </div>
        <div class="musicList-title">
          <div class="title">{{musicListTitle}}</div>
          <div class="author">
            <img :src="musicListAuthorAvatarUrl">
            <div class="nickname">{{musicListAuthorName}}</div>
            <div class="follow">+ 关注</div>
          </div>
        </div>
      </div>

      <div class="musicList-describe">
        <span>{{musicListDescribe}}</span>
        <i class="bi bi-chevron-right"></i>
      </div>

      <div class="other-info">
        <div class="reply">
          <i class="bi bi-reply-all-fill"></i>
          <span> {{musicListShareCount}}</span>
        </div>
        <div class="MusicListComment">
          <i class="bi bi-chat-dots-fill"></i>
          <span> {{musicListComment}}</span>
        </div>
        <div class="collect">
          <i class="bi bi-plus-square-fill"></i>
          <span> {{musicListCollect}}</span>
        </div>
      </div>
      
    </div>

    <div class="musicContainer">
      <div class="musicContainer-top">
        <div class="play-all">
          <div class="play">
            <i class="bi bi-play-fill"></i>
          </div>
          <div class="play-text">播放全部 <span> ({{musicListInner.length}})</span></div>
        </div>
        <div class="download-adjust">
          <i class="bi bi-download"></i>
          <i class="bi bi-list-check"></i>
        </div>
      </div>
      <ul class="musics">
        <li 
          v-for="item,index in musicListInner" 
          :key="item.id" 
          :data-id='item.id' 
          @click="getMusicId($event,item)" 
          :class="{'currentPlay': currentPlayId == item.id}"
        >
          <div class="num">
            <div :class="{'playing':true, 'animate-pause':isMusicPaused}" v-show="currentPlayId == item.id">
              <div style="--delay:0s; --height:3.5vw"></div>
              <div style="--delay:0.2s; --height:2.5vw"></div>
              <div style="--delay:0.3s; --height:1.5vw"></div>
            </div>
            <div class="number" v-show="currentPlayId != item.id">{{index + 1}}</div>
          </div>
          <div class="music">
            <div class="music-title">{{item.name}}</div>
            <div class="music-singer">
              <div class="type">超清母带</div>
              <div class="singer">{{item.ar[0].name}} - {{item.name}}</div>
            </div>
          </div>
          <div class="addNext"><i class="bi bi-plus-lg"></i></div>
          <div class="other"><i class="bi bi-three-dots-vertical"></i></div>
        </li>
      </ul>
    </div>

    <div class="music-cd" @click="MusicDrawer = true" >
        <img src="http://p3.music.126.net/qw0ussRsg5IMYkpsho0Qqg==/109951168728478129.jpg" alt="">
    </div>
  </div>
</template>

<script>
import {mapGetters, mapState,mapMutations,mapActions} from 'vuex'
import colorthief from 'colorthief'
import home from '@/request/api/home.js'
export default {
  name:'MusicListDetail',
  data(){
    return{
      loading:true,
      // 歌单信息
      musicListTitle:'',
      musicListCoverImg:'',
      musicListAuthorName:'',
      musicListAuthorAvatarUrl:'',
      musicListDescribe:'',
      musicListPlayed:'',
      musicListComment:'',
      musicListShareCount:'',
      musicListCollect:'',
      musicListTotalMusic:'',
      // 歌单内的歌曲
      // musicListInner:[]
    }
  },
  activated(){
    console.log('MusicListDetail被激活')
  },
  methods:{
    ...mapActions(['getMusicListAllMusic','getMusicURL']),
    backword(){
      this.$router.back()
    },
    async getImgColor(){
      const color = new colorthief()
      const musicListCoverImg = this.$refs.musicListCoverImg
      let res = await color.getPalette(musicListCoverImg, 3)
      const [c1, c2, c3] = res.map((c)=>{
          return `rgb(${c[0]}, ${c[1]}, ${c[2]})`
      })
      this.$refs.MusicListDetail.style.setProperty('--color',c2)
      // this.$refs.MusicListDetail.style.setProperty('--color2',c1)
    },

    getMusicId(e,item){
      this.currentPlayMusicIndex = this.musicListInner.indexOf(item)
      localStorage.setItem('currentPlayMusicIndex',JSON.stringify(this.currentPlayMusicIndex))
      console.log("////////",this.currentPlayMusicIndex)

      this.currentPlayId = e.currentTarget.dataset.id
      this.MusicDrawer = true
      this.getMusicURL(this.currentPlayId)
      // console.log(this.musicListInner.indexOf(this))
      // this.currentPlayList = this.musicListInner
      // localStorage.setItem('currentPlayList',JSON.stringify(this.currentPlayList))
    }

  },
  computed:{
    ...mapState(['musicListInner','isBottomNavShow','isMusicPaused']),
    MusicDrawer:{
      get(){
        return this.$store.state.MusicDrawer
      },
      set(value){
        this.$store.state.MusicDrawer = value
      }
    },
    currentPlayId:{
      get(){
        return this.$store.state.currentPlayId
        // return localStorage.getItem('currentPlayId')
      },
      set(value){
        this.$store.state.currentPlayId = value
      }
    },
    currentPlayMusicIndex:{
      get(){
        return this.$store.state.currentPlayMusicIndex
        // return localStorage.getItem('currentPlayId')
      },
      set(value){
        this.$store.state.currentPlayMusicIndex = value
      }
    },
    // currentPlayList:{
    //   get(){
    //     return this.$store.state.currentPlayList
    //   },
    //   set(value){
    //     this.$store.state.currentPlayList = value
    //   }
    // }

  },
  watch:{
    currentPlayId(value){
      localStorage.setItem('currentPlayId',JSON.stringify(+value))
    }
  },
  mounted(){
    console.log(this.musicListInner)
    this.getMusicListAllMusic(this.$route.query.id)
    // console.log(this.musicListInner)
    // if (this.$route.path === '/musicList') {
    //   this.$store.commit('BottomNavHide',false)
    // }

      // console.log(this.isBottomNavShow)
      // this.isBottomNavShow = false
    
    home.getMusicListDetail(this.$route.query.id)
        .then(
            res=>{
                // console.log(res.data)
                this.musicListTitle = res.data.playlist.name
                this.musicListCoverImg = res.data.playlist.coverImgUrl
                this.musicListAuthorName = res.data.playlist.creator.nickname
                this.musicListAuthorAvatarUrl = res.data.playlist.creator.avatarUrl
                this.musicListDescribe = res.data.playlist.description
                this.musicListShareCount = res.data.playlist.shareCount
                this.musicListComment = res.data.playlist.commentCount
                this.musicListCollect = res.data.playlist.subscribedCount
                this.loading = false
            },
            err=>{
                console.log(err.message)
                this.loading = false
                alert('网络错误')
            }
        )
    // console.log(this.$refs.musicListCoverImg.complete)
    //   this.getImgColor()
    
    
    // this.$refs.MusicListDetail.style.setProperty('--color','#90caf9')
  }
}
</script>

<style lang='less' scoped>
  .MusicListDetail{
    --color:#9d5759;
    --color2:#fff;
    // padding:0 5vw;
    // margin-top:5vw;
    // background-image: linear-gradient(var(--color),var(--color2));
    background: var(--color);
    .musicListDetail-top{
      width:100vw;
      height: 15vw;
      background:  var(--color);
      margin:0 auto;
      display: flex;
      line-height: 15vw;
      justify-content: space-between;
      font-size:4.5vw;
      position: fixed;
      left:50%;
      transform: translate(-50%);
      color:white;
      z-index: 10;
      .backword,.searchListMusic{
        margin:0 5vw;
        >*{
          margin: 0 2vw;
        }
      }
    }
    
    .musicList-info{
      margin-top:18vw; 
      // background: linear-gradient(var(--color),var(--color2));
      padding:0 5vw;
      color: white;
      .author-title{
        display: flex;
        .musicListCover-img{
          img{
            height: 25vw;
            aspect-ratio: 1;
            border-radius: 20px;
            box-shadow: 0 2px 10px #00000080;
          }
        }
        .musicList-title{
          margin-left: 3vw;

          .title{

          }
          .author{
            margin-top:3vw;
            display:flex;
            align-items: center;
            color:#ffffff80;
            font-size:3vw;
            &>*{
              margin-right: 2vw;
            }
            img{
              height: 5vw;
              aspect-ratio: 1;
              border-radius:50%;

            }
            .follow{
              padding:1vw 2vw;
              background: #ffffff2e;
              border-radius:10vw;
            }
          }
        }
      }
      .musicList-describe{
        margin-top:3vw;
        white-space: nowrap;
        overflow: hidden;
        // text-overflow: ellipsis;
        color:#ffffff80;
        font-size:3vw;
        position: relative;
        i{
          display: block;
          width:15vw;
          position: absolute;
          right: 0;
          top:0;
          text-align: end;
          background-image: linear-gradient(to right, transparent,  var(--color));
        }
      }
      .other-info{
        display: flex;
        justify-content: space-between;
        margin:5vw 0;
        font-size:3.5vw;
        &>*{
          width: 25vw;
          margin:0 2.5vw;
          padding:2.5vw 0;
          background: #ffffff2e;
          border-radius:25vw;
          text-align: center;
        }
        .collect{
          background: #fe2d4d;
        }
        .bi-reply-all-fill::before{
          transform:scaleX(-1)
        }
      }
    }
    .musicContainer{
      // position: relative;
      .musicContainer-top{
        display: flex;
        justify-content: space-between;
        align-items: center;
        padding:0 5vw;
        background: white;
        border-top-left-radius:10px;
        border-top-right-radius:10px;
        height: 10vw;
        position: sticky;
        top: 15vw;
        font-size:4vw;

        .play-all{
          display: flex;
          align-items: center;
          .play{
            height: 6vw;
            aspect-ratio: 1;
            border-radius: 50%;
            background: #fe2d4d;
            color:white;
            text-align: center;
            line-height: 6vw;
          }
          .play-text{
            span{
              color: #00000080;
            }
          }
        }

        .play-all>.play-text, .download-adjust>.bi-list-check{
          margin-left: 5vw;
        }
      }
      .musics{
        .currentPlay{
          color:#fe2d4d; 
           .num>.animate-pause>div{
              animation-play-state: paused;
            }
          .playing{
            width: 3.7vw;
            // background: red;
            display: flex;
            justify-content: space-between;
            align-items: flex-end;
            div{
              width: 0.7vw;
              // height: var(--height);
              height: 3vw;
              transform-origin: bottom;
              background: #fe2d4d;
              border-radius: 1vw;
                transform-origin: bottom;
                animation: run 0.8s infinite linear;
                animation-delay: var(--delay);
            }
          }
        }

        li{
          padding:3vw 5vw;
          display: flex;
          justify-content: space-between;
          align-items:center;
          background: #ffffff;
          font-size: 4vw;

          .num{
            width: 5vw;
          }

          .music{
            width: 65vw;
            overflow: hidden;
            white-space: nowrap;
            // text-overflow: ellipsis;
            .music-title, .singer{
              width: 100%;
              white-space: nowrap;
              overflow: hidden;
              text-overflow: ellipsis;
            }
          }
          .num, .addNext, .other{
            color: #00000050;
          }

          .music-singer{
            display: flex;
            font-size:3vw;
            color:#00000080;
            margin-top: 1vw;
            .type{
              border:0.1vw solid #c2ac79;
              padding:0.3vw 0.2vw 0.2vw 0;
              border-radius:4px;
              font-size: 2.5vw;
              color:#b8a66f;
              margin-right:1vw;
            }
          }
          
        }
      }
    }
    .music-cd{
      position: fixed;
      bottom: 5vw;
      left: 50%;
      transform: translate(-50%);
        img{
            height:12vw;
            aspect-ratio: 1;
            border-radius:50%;
            box-shadow: 5px 5px 10px #00000042;
        }
    }
    
  }
  
   @keyframes run{
    0%{
        transform: scaleY(1);
    }
    50%{
        transform: scaleY(0.3);
    }
    100%{
        transform: scaleY(1);
    }
   } 

</style>