import service from "..";

export default {
    getMusicSrc(id){
        return service({
            method:'get',
            url:`/song/url/v1?id=${id}&level=exhigh`
        })
    },
    getSingleMusicRecList(musicList, offset){
        return service({
            method:'get',
            url:`/playlist/track/all?id=${musicList[2]}&limit=20&offset=${offset}`
        })
    },
    getSingleMusicList(){
        return service({
            method:'get',
            url:'/top/playlist/highquality?limit=10'
        })
    },
    getSingleMusicRecommend(id){
        return service({
            method:'get',
            url:`/song/detail?ids=${id}`
        })
    },
    getSingleMusicRecommendCommentTotal(id){
        return service({
            method:'get',
            url:`/comment/music?id=${id}`
        })
    },
    getRecommendMusicList(){
        return service({
            method:'get',
            url:`/top/playlist/highquality?limit=9`
        })
    },
    getMusicListDetail(id){
        return service({
            method:'get',
            url:`/playlist/detail?id=${id}`
        })
    },
    getMusicListAllMusic(id){
        return service({
            method:"get",
            url:`/playlist/track/all?id=${id}`
        })
    },
    getMusicURL(id){
        return service({
            method:'get',
            url:`/song/url/v1?id=${id}&level=exhigh`
        })
    },
    getCurrentMusicDetail(id){
        return service({
            method:'get',
            url:`/song/detail?ids=${id}`
        })
    },
    getMusicLyric(id){
        return service({
            method:'get',
            url:`/lyric?id=${id}`
        })
    }
}
