<template>
    <div class="HomeContainer" id='APP'>
      <TopNav></TopNav>
      <Banner></Banner>
      <RecommendMusicList></RecommendMusicList>
      <NewMusicRecommend></NewMusicRecommend>
      <a class="return-top" href="#APP">TOP</a>
      <ul>
        <li v-for="single in singleMusicRecList" :key='single.id' class="single-music-rec-comp">
          <SingleMusicRecommend :single='single'></SingleMusicRecommend>
        </li>
      </ul>
      <div class="loading">加载中...</div>
      <!-- <BottomNav></BottomNav> -->
    </div>
</template>

<script>
  import axios from 'axios'

  import TopNav from './TopNav.vue'
  import Banner from './Banner.vue'
  import RecommendMusicList from './RecommendMusicList.vue'
  import NewMusicRecommend from './NewMusicRecommend.vue'
//   import BottomNav from './BottomNav.vue'
  import SingleMusicRecommend from './SingleMusicRecommend.vue'


  export default {
    components:{
      TopNav,Banner,RecommendMusicList,NewMusicRecommend,SingleMusicRecommend
    },
    data(){
      return {
        singleMusicList:[], // 存放歌单
        singleMusicRecList:[], // 歌单内的歌曲
        offset:0,
      }
    },
    methods:{
      loading(){
        const load = this.$el.querySelector('.loading')
        const intersectionObserver = new IntersectionObserver((entries,observe) => {
        // 如果 intersectionRatio 为 0，则目标在视野外，
        // 我们不需要做任何事情。
            // console.log(entries)
            if (!entries[0].isIntersecting){
                // load.classList.remove('active')
                return 
            }else{
                // load.classList.add('active')
                this.offset += 20
                axios.get(`http://192.168.0.106:3000/playlist/track/all?id=${this.singleMusicList[1]}&limit=20&offset=${this.offset}`)
                  .then(
                    response=>{
                      this.singleMusicRecList.push(...response.data.songs)
                      console.log('总数为',this.singleMusicRecList.length)
                    },
                    err=>{
                      console.log(err.message)
                    }
                  )
            }

        });
        intersectionObserver.observe(load);
      }
    },
    mounted(){
      this.loading()
        axios.get('http://192.168.0.106:3000/top/playlist/highquality?limit=10')
            .then(
                result=>{
                    // console.log(result.data.playlists[0].name)
                    result.data.playlists.forEach(e=>{
                      this.singleMusicList.push(e.id)
                    })
                    // console.log(this.singleMusicList)
                    // this.singleMusicList = result.data.playlists
                    // console.log(result.data)
                },
                err=>{
                    console.log(err.message)
                }
            ).then(
              response =>{
                  axios.get(`http://192.168.0.106:3000/playlist/track/all?id=${this.singleMusicList[1]}&limit=20&offset=0`)
                  .then(
                      response=>{
                          this.singleMusicRecList.push(...response.data.songs)
                          console.log(response.data.songs)
                      },
                      err=>{
                        console.log(err.message)
                      }
                  )
              },
              err=>{
                console.log(err.message)
              }
            )
    }
  };
</script>

<style scoped>
  /* @import url("https://cdn.jsdelivr.net/npm/bootstrap-icons@1.10.0/font/bootstrap-icons.css"); */

  .single-music-rec-comp:last-child{
    margin-bottom:10vw ;
  }
  .loading{
    text-align: center;
    font-size:4vw;
    height: 40vw;
    width:100vw;
  }
  .return-top{
    position:sticky;
    top:150vw;
    left: 87vw;
    display: block;
    height: 10vw;
    aspect-ratio: 1;
    background: #ddd;
    border-radius: 50%;
    text-align: center;
    line-height: 10vw;
    z-index: 10;
    float: left;
  }
</style>