<template>
  <div class="Lyric" @click="showLrc">
    <div class="no-data" v-show="!currentPlayLrc" style="position:relative; top:20vh">暂无数据，请检查网络</div>
    <div class="lrc-container" v-show="currentPlayLrc">
      <ul>
        <li
          v-for="(line,index) in LrcLineArr" 
          :key="index"
          :data-index='index'
          
        >
          <span>{{line.word}}</span>
        </li>
      </ul>
    </div>
  </div>
</template>

<script>
import { mapGetters, mapState, mapMutations, mapActions } from "vuex";
export default {
  methods:{
    showLrc(e){
        this.isLrcShow = !this.isLrcShow
        // console.log(this.LrcLineArr)
    }
  },
  computed:{
    ...mapState(['currentPlayLrc']),
    isLrcShow:{
      get(){
        return this.$store.state.isLrcShow
      },
      set(value){
        this.$store.state.isLrcShow = value
      }
    },
    LrcLineArr(){
        const Lrc = this.currentPlayLrc
        const  lines = Lrc.split('\n')
        let result = []
        for(let i = 0; i<lines.length; i++){
            let lineStr = lines[i]
            let parts = lineStr.split("]")
            let timeStr = parts[0].substring(1)
            let timeResult = timeStr.split(':')
            let min = +timeResult[0] * 60
            let sec = +timeResult[1]
            let lineObj = {
                time:min + sec,
                word: parts[1],
            }
            result.push(lineObj)
        }
        return result
    },
  },
  mounted(){
    // console.log(this.currentPlayLrc == undefined)
  }
}
</script>

<style lang='less' scoped>
  .Lyric{
    height:63vh;
    width: 90vw;
    margin:0 auto;
    text-align: center;
    position:relative;

    .lrc-container{
      width: 90vw;
      height: 56vh;
      // background: rgb(255, 157, 157);
      overflow-y: scroll;
      overflow-x: hidden;
      position:absolute;
      top:50%;
      transform: translateY(-50%);

      &::-webkit-scrollbar {
        display: none;
      }
      ul{
        li{
          min-height:6.5vh;
          transition:.3s;
          color:#0000005c;
          display: flex;
          justify-content: center;
          align-items: center;
        }
        
        .active{
            // transform-origin: 0 0;
            transform:scale(1.2);
            color:black;
        }
      }
    }
    
  }

</style>