<template>
  <div class="MusciPlayer clearfix" ref='MusicPlayer'>
    <audio 
      id='audio' 
      :src="musicURL" 
      @ended="musicEnd"
      @pause="musicPause"
      @play='musicPlay'
      @loadedmetadata='musicLoaded'
      @timeupdate="musicTimeUpdate"
      style="position:absolute;right:0;"
    ></audio>
    <div class="top-nav">
      <div class="back" @click="MusicDrawer=false">
        <i class="bi bi-chevron-down"></i>
      </div>
      <div class="music-infos">
        <h3 class="music-name">{{currentPlayDetail.musicName}}</h3>
        <div class="singer-name">{{currentPlayDetail.singerName}}</div>
      </div>
      <div class="share"><i class="bi bi-share" ></i></div>
    </div>
    <transition name="show">
      <MusicCD v-show="!isLrcShow" :currentPlayDetail='currentPlayDetail'></MusicCD>
    </transition>
    <transition name='show2'>
      <Lyric v-show="isLrcShow"></Lyric>
    </transition>
      
    
    <MusicPlayerBottom></MusicPlayerBottom>
    <el-drawer
      title="我是里面的"
      :append-to-body="true"
      :visible.sync="playListDrawer"
      direction='btt'
      size='65%'
      custom-class='playListDrawer'
      :with-header="false"
    >
      <PlayingList></PlayingList>
    </el-drawer>


  </div>
</template>

<script>
import { mapGetters, mapState, mapMutations, mapActions } from "vuex";
import Lyric from './Lyric.vue';
import MusicCD from './MusicCD.vue'
import MusicPlayerBottom from './MusicPlayerBottom.vue';
import PlayingList from './PlayingList.vue';
export default {
  data() {
    return {
      value: 10,
      // drawer: false,
    };
  },
  components:{
    MusicCD,MusicPlayerBottom,Lyric,PlayingList
  },
  watch: {
    currentPlayId(value) {
      // console.log("-----currentPlayId 改变了")
      audio.autoplay = true
      // audio.play()
      // this.$nextTick(()=>{
      //   if(audio.paused){
      //     audio.play()
      //   }
      // })
      this.getCurrentMusicDetail(value)
      this.getMusicLyric(value)

      localStorage.setItem('currentPlayId',JSON.stringify(+value))
    },
    currentPlayMusicIndex(){
      console.log('&&&&&&&',this.currentPlayMusicIndex)
    }
  },
  computed: {
    ...mapState(['isLrcShow','musicURL','musicListInner','currentPlayList','currentPlayDetail']),
    MusicDrawer:{
      get(){
        return this.$store.state.MusicDrawer
      },
      set(value){
        this.$store.state.MusicDrawer = value
      }
    },
    playListDrawer:{
      get(){
        return this.$store.state.playListDrawer
      },
      set(value){
        this.$store.state.playListDrawer = value
      }
    },
    currentPlayMusicIndex:{
      get(){
        return this.$store.state.currentPlayMusicIndex
      },
      set(value){
        this.$store.state.currentPlayMusicIndex = value
      }
    },
    currentPlayId:{
      get(){
        return this.$store.state.currentPlayId
      },
      set(value){
        this.$store.state.currentPlayId = value
      }
    },
    isMusicPaused:{
      get(){
        return this.$store.state.isMusicPaused
      },
      set(value){
        this.$store.state.isMusicPaused = value
      }
    },
  },
  mounted() {
    // console.log(JSON.parse(localStorage.getItem("currentPlayList")));
    // console.log(this.currentPlayList)
    this.getCurrentMusicDetail(this.currentPlayId)
    this.getMusicLyric(this.currentPlayId)
    this.$bus.$on('getMusicImgColor',(value)=>{
      console.log(value)
      this.$refs.MusicPlayer.style.setProperty('--color1',value)
      // console.log(this.$refs.MusicPlayer)
    })
  },
  activated() {
    console.log("musicPlayer被激活");
  },
  methods: {
    ...mapActions(['getMusicURL','getCurrentMusicDetail','getMusicLyric']),
    musicEnd(){
      this.currentPlayMusicIndex ++
      if(this.currentPlayMusicIndex >= (this.musicListInner.length - 1)){
        this.currentPlayMusicIndex = 0
      }
      console.log(this.currentPlayMusicIndex)
      localStorage.setItem('currentPlayMusicIndex',JSON.stringify(this.currentPlayMusicIndex))

      this.currentPlayId = this.musicListInner[this.currentPlayMusicIndex].id
      
      this.getMusicURL(this.musicListInner[this.currentPlayMusicIndex].id)
      this.$nextTick(()=>{
      //   audio.play()
        if(audio.paused){
          audio.play()
          audio.autoplay = true
        }
      })
    },
    musicPause(){
      this.isMusicPaused = true
    },
    musicPlay(){
      this.isMusicPaused = false
    },
    musicLoaded(){
      this.$bus.$emit('getMusicTime',audio.duration)
    },
    musicTimeUpdate(){
      this.$bus.$emit('getUpdateTime',audio.currentTime)
    }
    // audioLoad(){
    //   if(audio.paused){
    //     audio.play()
    //   }
    // }
  },
};
</script>

<style lang='less' scoped>
    * {
      padding: 0;
      margin: 0;
    }
    .MusciPlayer {
      --color1:#c98c8c;
      --color2:#eef7fe;
      height: 100vh;
      // background: red;
      background: linear-gradient(var(--color1) -200%, var(--color2));
    }
    .top-nav {
      display: flex;
      justify-content: space-between;
      align-items: center;
      font-size: 2vh;
      height:6vh;
      width:90vw;
      margin: 2vh auto ;
      text-align: center;
      // padding: 2vh;

      .back,.share {
        height: 5vh;
        aspect-ratio: 1;
        background: #eef1fa;
        border-radius: 50%;
        line-height: 5vh;
        box-shadow: 5px 5px 7px #0000001c, inset 1px 1px 0px #ffffff;
        // font-size: 4vh;
      }

      .bi {
        font-size: 3vh;
      }
    }
    .show-enter-active,.show2-enter-active{
      transition:opacity 0.8s;
    }
    .show-enter,.show2-enter{
      opacity: 0;
    }
    .show-leave,.show2-leave{
      opacity: 0;
    }
    .playListDrawer{
      background-color: yellow;
      border-radius: 20px;
    }
    
</style>