import VueRouter from "vue-router";
import HomeContainer from '../pages/home/HomeContainer'
import MusicPlayer from '../pages/player/MusicPlayer.vue'
import Myself from '../pages/myself/Myself'
import TopNav from '../pages/home/TopNav'
import MusicListDetail from '../pages/home/MusicListDetail.vue'
import store from "@/store";


const router =  new VueRouter({
    routes:[
        { path: '/', redirect: '/home' }, //将根路径重定向到/home路由
        {
            name:'home',
            path:'/home',
            component:HomeContainer,
            // children:[
            //     {
            //         name:'musicList',
            //         path:'musicList',
            //         component:MusicListDetail,
            //     }
            // ],
        },
        // {
        //     name:'player',
        //     path:'/player',
        //     component:MusicPlayer
        // },
        {
            path:'/topnav',
            component:TopNav
        },
        {
            path:'/myself',
            component:Myself
        },
        {
            name:'musicList',
            path:'/musicList',
            component:MusicListDetail,
        }
    ]
})

router.afterEach((to,from)=>{
    if(to.path=='/musicList'){
        setTimeout(()=>{
            store.state.isBottomNavShow=false
        },300)
        // next()
    }else{
        setTimeout(()=>{
            store.state.isBottomNavShow=true
        },300)
    //   next()
    }
})

export default router