// 该文件用于创建vuex中最核心的store
import vue from 'vue'
import vuex from 'vuex'
import axios from 'axios'
import home from '@/request/api/home.js'

vue.use(vuex)


//准备actions——用于响应组件中的动作
const actions = {
    // 获取主页推荐列表内的歌曲
    getSingleMusicRecList(context,{musicList, offset}){
        // let res = await home.getSingleMusicRecList(musicList, offset)
        // context.commit("GetSingleMusicRecList",(res.data.songs))
        home.getSingleMusicRecList(musicList, offset).then(
            res=>{
                context.commit("GetSingleMusicRecList",(res.data.songs))
            },
            error=>{
                console.log(error.message)
            }
        )
    },
    // 获取主页推荐列表
    getSingleMusicList(context, value){
        home.getSingleMusicList()
            .then(
                res=>{
                    context.commit("getSingleMusicList",res.data.playlists)
                },
                error=>{
                    console.log(error.message)
                }
            ).then(
                res=>{
                    home.getSingleMusicRecList(context.state.singleMusicList, context.state.offset)
                        .then(
                            res =>{
                                context.commit("GetSingleMusicRecList",(res.data.songs))
                            },
                            error=>{
                                console.log(error.message)
                            }
                        )
                }
            )
    },
    getMusicListAllMusic(context,value){
        home.getMusicListAllMusic(value)
            .then(
                res=>{
                    // console.log(res.data.songs)
                    context.commit('GetMusicListAllMusic',res.data.songs)
                }
            )
    },
    getMusicURL(context,value){
        home.getMusicURL(value)
            .then(
                res=>{
                    // console.log(res.data.data)
                    localStorage.setItem('musicURL',res.data.data[0].url)
                    context.commit('GetMusicURL',res.data.data[0].url)
                }
            )
    },
    getCurrentMusicDetail(context,value){
        home.getCurrentMusicDetail(value)
            .then(
                res=>{
                    // console.log(res.data.songs[0])
                    context.commit('GetCurrentMusicDetail',res.data.songs[0])
                }
            )
    },
    getMusicLyric(context,value){
        home.getMusicLyric(value)
            .then(
                res=>{
                    // console.log(res.data.lrc.lyric)
                    context.commit('GetMusicLyric',res.data.lrc.lyric)
                }
            )
    }
}


//准备mutations——用于操作数据（state）
const mutations = {
    GetData(state,value){
        state.musicSrc = value
        state.playIndex ++
    },
    GetSingleMusicRecList(state,value){
        state.offset += 20
        state.singleMusicRecList.push(...value)
        // console.log(state.singleMusicRecList)
    },
    getSingleMusicList(state,value){
        value.forEach(e => {
            state.singleMusicList.push(e.id)
        });
    },
    GetMusicListAllMusic(state,value){
        state.musicListInner = value
        localStorage.setItem('musicListInner',JSON.stringify(state.musicListInner))
    },
    BottomNavHide(state,value){
        state.isBottomNavShow = value
    },
    GetMusicURL(state,value){
        state.musicURL = value
    },
    GetCurrentMusicDetail(state,value){
        state.currentPlayDetail.musicName = value.name
        state.currentPlayDetail.singerName = value.ar[0].name
        state.currentPlayDetail.musicCoverImg = value.al.picUrl
    },
    GetMusicLyric(state, value){
        state.currentPlayLrc = value
    }
}


//准备state——用于存储数据
const state = {
    musicSrc:'123456',
    singleMusicList:[], // 存放歌单
    singleMusicRecList:[], // 歌单内的歌曲
    offset:0,
    isBottomNavShow:true,
    isLrcShow:false,
    MusicDrawer:false, // 播放页面弹出框
    playListDrawer:false, // 播放列表弹出框
    musicListInner: JSON.parse(localStorage.getItem('musicListInner')) || [], // 播放列表
    currentPlayList: JSON.parse(localStorage.getItem('currentPlayList')) || [], // 暂时弃用
    currentPlayId: localStorage.getItem('currentPlayId') || 0,
    musicURL: localStorage.getItem('musicURL') || '',
    currentPlayMusicIndex: localStorage.getItem('currentPlayMusicIndex') || 0,
    isMusicPaused:true,
    currentPlayDetail:{
        musicName:'',
        singerName:'',
        musicCoverImg:'',
    },
    currentPlayLrc:'',
}


// 准备getters用于将state中的数据进行加工
const getters = {
    
}
export default new vuex.Store({
    actions,
    mutations,
    state,
    getters
})