<template>
  <div class="music-player-bottom">
    <div class="range-time">
        <div class="block">
            <el-slider 
                v-model="musicTime" 
                :show-tooltip="false" 
                @change="timeChange" 
                :max="elSliderMax"
                style='height:4vh'
            ></el-slider>
            <div class="time">
                <span>{{beginMin}}:{{beginSec}}</span>
                <span>{{endMin}}:{{endSec}}</span>
            </div>
        </div>
    </div>
    <div class="controls-icon">
        <div class="last-music" @click="lastMusic"><i class="bi bi-skip-start-fill"></i></div>
        <div class="pause-play" @click="pauseMusic">
            <i :class="{'bi-caret-right-fill': isMusicPaused, 'bi-pause-fill': !isMusicPaused}"></i>
        </div>
        <div class="next-music" @click="nextMusic"><i class="bi bi-skip-end-fill"></i></div>
    </div>
    <div class="other-icons">
        <div class="play-model"><i class="bi-repeat"></i></div>
        <div class="comment">21评论 <i class="bi bi-chevron-double-up"></i></div>
        <div class="music-list" @click="playListDrawer = true"><i class="bi-list"></i></div>
    </div>
  </div>
</template>

<script>
import { mapGetters, mapState, mapMutations, mapActions } from "vuex";
export default {
    data(){
        return {
            musicTime: 0,
            endSec:0,
            endMin:0,
            beginSec:0,
            beginMin:0,
            musicEndTime:JSON.parse(localStorage.getItem('musicEndTime')) || 0,
            elSliderMax:100,
        }
    },
    mounted(){
        this.$bus.$on('getMusicTime',(value)=>{
            // this.musicTime = value
            this.elSliderMax = value
            audio.currentTime = this.musicEndTime
            this.musicTime = this.musicEndTime
            this.endSec = parseInt(audio.duration % 60)
            this.endMin = parseInt((audio.duration / 60) % 60)

            if(this.endSec <10){
                this.endSec = '0'+this.endSec
            }
        }),
        this.$bus.$on('getUpdateTime',(value)=>{
            this.beginMin = parseInt((audio.currentTime / 60) % 60)
            this.beginSec = parseInt(audio.currentTime % 60)
            this.musicEndTime = value
            this.musicTime = value
            if(this.beginSec < 10){
                this.beginSec = '0' + this.beginSec
            }
        })
    },
    methods:{
        ...mapActions(['getMusicURL']),
        lastMusic(){
            this.currentPlayMusicIndex --
            if(this.currentPlayMusicIndex <= -1){
                this.currentPlayMusicIndex = this.musicListInner.length - 1
            }
            localStorage.setItem('currentPlayMusicIndex',JSON.stringify(this.currentPlayMusicIndex))

            this.currentPlayId = this.musicListInner[this.currentPlayMusicIndex].id
            
            this.getMusicURL(this.musicListInner[this.currentPlayMusicIndex].id)
            this.$nextTick(()=>{
            //   audio.play()
                if(audio.paused){
                    audio.play()
                    audio.autoplay = true
                }
            })
        },
        pauseMusic(){
            if(audio.paused){
                audio.play()
            }else{
                audio.pause()
            }
        },
        nextMusic(){
            this.currentPlayMusicIndex ++
                console.log(this.musicListInner.length)
            if(this.currentPlayMusicIndex >= (this.musicListInner.length - 1)){
                this.currentPlayMusicIndex = 0
            }
            localStorage.setItem('currentPlayMusicIndex',JSON.stringify(this.currentPlayMusicIndex))

            this.currentPlayId = this.musicListInner[this.currentPlayMusicIndex].id
            
            this.getMusicURL(this.musicListInner[this.currentPlayMusicIndex].id)
            this.$nextTick(()=>{
            //   audio.play()
                if(audio.paused){
                    audio.play()
                    audio.autoplay = true
                }
            })
        },
        timeChange(){
            audio.currentTime = this.musicTime
        }
    },
    computed:{
         ...mapState(['musicListInner']),
        playListDrawer:{
            get(){
                return this.$store.state.playListDrawer
            },
            set(value){
                this.$store.state.playListDrawer = value
            }
        },
        currentPlayMusicIndex:{
            get(){
                return this.$store.state.currentPlayMusicIndex
                // return localStorage.getItem('currentPlayId')
            },
            set(value){
                this.$store.state.currentPlayMusicIndex = value
            }
        },
        currentPlayId:{
            get(){
                return this.$store.state.currentPlayId
            },
            set(value){
                this.$store.state.currentPlayId = value
            }
        },
        isMusicPaused:{
            get(){
                return this.$store.state.isMusicPaused
            },
            set(value){
                this.$store.state.isMusicPaused = value
            }
        },
    },
    watch:{
        musicEndTime(value){
            localStorage.setItem("musicEndTime",JSON.stringify(value))
        }
    }
}
</script>

<style lang='less' scoped>
    .music-player-bottom{
        width:85vw;
        // height:20vh;
        margin:1vh auto;
        .range-time{
            margin:2vh 0 0 0;
            .block{
                width:85vw;
                .time{
                    display: flex;
                    justify-content: space-between;
                    color:#a8b0b8;
                }                
            }
        }
        .controls-icon{
            display: flex;
            justify-content: space-around;
            align-items: center;
            text-align: center;
            margin: 1vh 0 0 0;
            div{
                height:10vh;
                aspect-ratio: 1;
                border-radius:50%;
                background: #f6f9fe;
                font-size:5vh;
                box-shadow:  5px 5px 12px #3636361d, inset 2px 2px 2px #fff, -5px -5px 15px #ffffff;
                line-height:10vh;
            }
            .last-music, .next-music{
                height:7vh;
                line-height: 7vh;
                font-size: 3vh;
            }
        }
        .other-icons{
            display: flex;
            justify-content: space-between;
            align-items: center;
            margin:2vh 0 0 0;
            font-size:3vh;
            .comment{
                font-size:1.5vh;
                color:#a8b0b8;
            }
        }
    }

</style>