<template>
  <div class="recommend-music-list">
    <div class="recommend">
        <div class="re-title">
            <h3> 推荐频道</h3>
            <div><i class="bi bi-arrow-repeat"></i></div>
        </div>
        <div class="more">
            查看更多<i class="bi bi-chevron-right"></i>
        </div>
    </div>

    <div class="lists-bar">
        <ul class="lists">
            <router-link 
                v-for="item in recommendMusicList" :key="item.id"
                :to="{
                    path:'/musicList',
                    query:{
                        id:item.id,
                    }
                }"
            >
                <li ref="item">
                    <div class="title">{{item.name}}</div>
                </li>
            </router-link>
            
        </ul>
    </div>
    <!-- <div>{{testData}}</div> -->
    
  </div>
</template>

<script>
import axios from 'axios'
import home from '@/request/api/home'
export default {
    data(){
        return {
            recommendMusicList:[
                {id:'01',name:'网络错误'},
                {id:'02',name:'网络错误'},
                {id:'03',name:'网络错误'},
                {id:'04',name:'网络错误'},
                {id:'05',name:'网络错误'},
                {id:'06',name:'网络错误'},
                {id:'07',name:'网络错误'},
                {id:'08',name:'网络错误'},
                {id:'09',name:'网络错误'},
            ],
            // testData:''
        }
    },
    methods:{
        // getMusicListData(id){
        //     axios.get(`http://192.168.0.106:3000/playlist/detail?id=${id}`)
        //         .then(
        //             res=>{
        //                 console.log(res.data)
                       
        //             }
        //         )
        // }
    },
    mounted(){
        // this.test()
        // axios.get('http://192.168.0.103:3000/top/playlist/highquality?limit=9')
        home.getRecommendMusicList()
            .then(
                result=>{
                    this.recommendMusicList = result.data.playlists
                    // console.log(this.recommendMusicList)
                    this.$nextTick(()=>{
                        this.recommendMusicList.forEach((e,index)=>{
                            this.$refs.item[index].style.backgroundImage = `url(${e.coverImgUrl})`
                        })
                    })
                    
                },
                err=>{
                    this.recommendMusicList = '网络错误'
                    console.log(err.message)
                }
            )
    }
}
</script>

<style lang='less' scoped>
    .recommend-music-list{
        .recommend{
            display:flex;
            justify-content: space-between;

            .re-title,.more{
                margin:2vw 5vw;
            }
            .re-title{
                display: flex;
                font-size: 4.5vw;
                div{
                    height:6vw;
                    aspect-ratio: 1;
                    background: #f2f7fd;
                    border-radius:50%;
                    box-shadow:1px 1px 8px 0px #afbfd2, -4px -3px 8px #ffffff;
                    font-size:4vw;
                    text-align:center;
                    margin:0 2vw;
                    i{
                        line-height: 6vw;
                    }
                }
            }
            .more{
                padding:1vw;
                color:#5e6568;
                font-size: 2.5vw;
                background: #f2f7fd;
                border-radius:30px;
                box-shadow:2px 3px 8px #5b5b5b50, -2px -2px 11px #fff;
                line-height: 5vw;
            }
        }
        .lists-bar{
            width:100vw;
            height: 50vw;
            overflow-y: scroll;
        }
        .lists{
            list-style:none;
            width:calc(5vw + calc(30vw * 9));
            font-size:3vw;
            li{
                height:40vw;
                width: 25vw;
                float:left;
                margin:2vw 0 0 5vw;
                border-radius: 20px;
                background-size: cover;
                background-repeat: no-repeat;
                box-shadow:8px 5px 9px #5b5b5b45, -3px -4px 11px #fff,inset 6px 2px 9px #ffffff9c;
                overflow: hidden;
                // &:last-child{
                //     margin-right: 5vw;
                // }
                .title{
                    height: 34vw;
                    width: 19vw;
                    padding: 3vw;
                    color:white;
                    border-radius:20px ;
                    backdrop-filter: brightness(0.7);
                    font-size:3.3vw;
                }
            }

        }
    }

</style>